<docs>
  # Listing Thematique
  > The Listing section for thematique page, with title description and a listing of object per categorye
  
  @author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, tryOnUnmounted, until } from "@vueuse/core";
import Arrow from "assets/svg/arrow.svg?raw";
import Plus from "assets/svg/plus.svg?raw";
import scrollStore from 'components/SmoothScroll/store';
import IconPlay from 'components/UiKit/Video/assets/play.svg?raw';
import { store as themeStore } from "pages/Thematique/store";
import emitter, { EVENTS } from "plugins/store/events";
import { store as appStore } from "store/app";
import { store as interfaceStore } from "store/interface";
import { store as loaderStore } from "ui/Loader/store";
import useFlow from "utils/flow";
import { gsap } from "vendors/gsap";
import { computed, defineComponent, inject, toRefs } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute, useRouter } from 'vue-router';
import { getSVGConfig } from "../webgl/config";

export default defineComponent({
  name: "HeaderThematique",
  props: {
    variant: {
      type: String,
      default: "default"
    },
  },
  setup(props) {
    const route = useRoute();
    const router = useRouter();
    const { t } = useI18n();
    const { done } = toRefs(loaderStore.state);
    const el = templateRef<HTMLElement>("el");
    const BgAnim = templateRef<HTMLElement>("BgAnim");
    const disabled = computed(() => appStore.disabled);
    const { onInitUi } = useFlow();

    const SVGConfig = getSVGConfig(props.variant);

    let outroTimeline : GSAPTimeline;

    onInitUi( async () => {
      await until(done).toBe(true);

      emitter.on(EVENTS.SHOW_LISTING, animateIn);
      emitter.on(EVENTS.THEME_SWITCHING, animateOut);
    })

    const generateOutroTimeline = () => {
      outroTimeline = gsap.timeline({
        scrollTrigger: {
          trigger: el.value,
          start:"95% bottom",
          end: "bottom bottom",
          scrub: true,
          onUpdate: ({progress}) => {
            if(progress >= 0.6 && !themeStore.showThemeNav) {
                themeStore.toggleThemeNav(true);
            }else if(progress < 0.6 && themeStore.showThemeNav) {
                themeStore.toggleThemeNav(false, true);
            }
          }
        }
      })
        .to(el.value.querySelectorAll(".listing-wrapper"), { borderColor:SVGConfig.svg.color, opacity:.5, marginLeft: 100, marginRight: 100, duration: 2 }, 0)
    }

    // Tween to display elements 
    const animateIn = () => {
      const easing = themeStore.fromListing ? "power4.out" : "power2.out";
      const speed = themeStore.fromListing ? 1.25 : 2.75;
      const delay = themeStore.fromListing ? 0 : 1;
      const offset = themeStore.fromListing ? 1.5 : 0;

      gsap.timeline({
        onComplete: () => {
          emitter.emit(EVENTS.HIDE_SPRITESHEET);
          
        }
      }
      )
        .add('start')
        .add(() => {
          el.value.classList.remove("hidden");
        }, 0)
        .fromTo(BgAnim.value, { ease: easing, scale: 0.65 }, { scale: 1, yPercent: 0, duration: speed }, `start+=${delay}`)
        .add(() => {
          BgAnim.value?.classList.add("hidden");
        }, `start+=${5 - offset}`)
        .from(el.value, { yPercent: 100, ease: easing, duration: speed }, `start+=${delay}`)
        .add(() => {
          themeStore.toggleIsListing(true);
          generateOutroTimeline();
        }, `start+=${(speed + 1) - offset}`)
    }

    // Tween to hide elements 
    const animateOut = () => {
      const el = document.querySelector(".listingSection") as HTMLElement;
      gsap.timeline(
        {
          onComplete: () => {
            themeStore.toggleIsListing(false);
          }
        }
      )
        .to(el, { opacity: 0, ease: "power4.out", duration: 1 }, 0)
        .add(() => {
          el.classList.add("hidden");
        }, 1)
    }

    const page = inject<Partial<{
      url: string,
      listing: ListingData,
    }>>('page')

    const scrollTarget = () => {
      const target = el.value.querySelector('.listing-section') as HTMLElement;
      if (target) scrollStore.scrollTo(target);
    }

    const onSelectObject = (e: PointerEvent, item: ObjectSingle) => {
      e.preventDefault();

      appStore.disableApp(true);

      //hide the listing
      gsap.timeline()
        .to(el.value, { opacity: 0, ease: "power4.out", duration: 1 }, 0)
        .add(() => {
          emitter.emit(EVENTS.CLICK_OBJECT);
        }, .2)
        .add(() => {
          router.push({
            name: 'object',
            params: {
              thematique: route.params.slug,
              slug: item.slug
            }
          })
        }, .8)
    }

    tryOnUnmounted(() => {
      outroTimeline?.scrollTrigger?.kill();
      emitter.off(EVENTS.SHOW_LISTING, animateIn);
    })

    return {
      ...toRefs(props),
      t,
      page,
      disabled,
      el,
      BgAnim,
      animateIn,
      animateOut,
      scrollTarget,
      onSelectObject,
      Arrow,
      Plus,
      route,
      IconPlay,
      imageExtension: computed( () => interfaceStore.hasWebP ? '.webp' : '.png'),
      borderColor: computed( () => `rgb(${SVGConfig.bgColor})`)
    };
  },
});
</script>

<template>
  <section v-if="page && page.listing"
           ref="el"
           class="listingSection hidden"
           :data-variant="variant"
           :data-dark="false"
           :data-disabled="disabled"
  >
    <header class="header-list">
      <h1 class="title" v-html="page.listing.title" />
      <div class="description" v-html="page.listing.description" />
      <ui-button class="btn"
                 theme="icon"
                 :variant="variant"
                 :icon="Arrow"
                 @click="scrollTarget"
      />
    </header>

    <div class="listing-wrapper">
      <div v-for="(sect, index) in page.listing.section"
           :key="`section-${index}`"
           class="listing-section"
      >
        <div class="top">
          <h2 class="title-section" v-html="sect.title" />
          <div class="index">
            {{ (index + 1 < 10 ? '0' : '') + (index + 1) }}
          </div>
        </div>

        <div class="bottom">
          <article v-for="(item, indexItm) in sect.objects"
                   :key="`item-${indexItm}`"
                   class="listing-obj"
                   :data-type="item.type"
          >
            <div class="img-left">
              <span v-if="item.type === 'video'"
                    class="play-btn-img"
                    v-html="IconPlay"
              />
              <ui-picture :src="`/thematiques/${route.params.slug}/contenu/thumbnails/${item.id}${imageExtension}`"
                          :cover="item.type === 'video' ? 'cover' : 'contain'"
                          :parallax="false"
                          :lazy="false"
              />
            </div>
            <div class="content">
              <h1 class="title-obj" v-html="item.title" />
              <ui-button ref="Discover"
                         class="cta"
                         theme="primary"
                         :variant="variant"
                         label="explorer l’objet"
                         :icon="Plus"
                         tag="a"
                         :to="{
                           name: 'object',
                           params: {
                             thematique: route.params.slug,
                             slug: item.slug
                           }
                         }"
                         @click="onSelectObject($event, item)"
              />
            </div>
          </article>
        </div>
      </div>
    </div>
    <div ref="BgAnim" class="bg-anim" />
  </section>
</template>

